.heading{
    color: rgb(4, 4, 87);
}
.subheading{
    font-size: 20px;
}
.tilecontainer{
    height: fit-content;
    width: 100%;
    box-shadow: 0 3px 6px black;
    margin-bottom: 20px;
    border: 2px solid white;
    border-radius: 0%;
    padding: 12px;
    
}
.border-radius{
    border-radius: 20px;
    height: 8em;
}
.pad-left{
    padding-left: 10%;
}
.tileheading{
    text-align: left;
    color:black;
    font-size: large;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    
}
.tilesubheading{
    text-align: left;
    font-size: medium;
    color:grey;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}