.filter-box{
    border: 2px solid white;
    background-color: white;
    box-shadow: 0 3px 6px black;
    padding: 4px 4px 4px 25px;
    color: rgb(30, 71, 255);
    font-weight: bold;
    margin-bottom: 15px;
}
.item-box1{
    
    background-color: white;
    box-shadow: 0 3px 6px black;
    padding: 20px 20px 20px 20px;
    margin: 0px 0px 35px;
    width: 100%;
}



.item-heading{
    font-weight: bold;
    color: rgb(7, 7, 88);
    font-size: x-large;
}
.item-subheading{
    
    color: rgb(26, 26, 148);
    padding: 4px;
}
.img-style{
    border-radius: 25px;
    height: 8em;
    padding: 5px;
    
}
.mg-in-item-box-text{
    padding: 1px;
    text-align: left;
    
} 
.item-address{
    color: grey;
    padding: 6px;
    font-size: 0.9em;
}
.pd-item-hd{
    padding: 0px 0px 6px;
}
.rs1{
    color: grey;
}
.rs2{
    color: rgb(26, 26, 148);
    font-weight: bold;
}