.container-fluid {
    /* padding-right: 15px; */
    /* padding-left: 15px; */
    margin-right: auto;
    margin-left: auto;
    border-radius: 0%;
}
.jumbotron{
    background-image: url("https://i.ibb.co/ySvXT89/food-wallpaper.png");
    background-size: cover;
    color: white;
    border-radius: 0%;
}
.logo {
    
    background-color: white;
    height: 70px;
    width: 70px;
    color: red;
    border: 1px solid white;
    border-radius: 50%;
    text-align: center;
    margin: auto;
    margin-bottom: 30px;
}
.mh{
    margin-top: 16px;
}


.jumbotron p {
    margin-bottom: 35px;
    font-size: 29px;
    font-weight: 200;
}
.form-control{
    display: inline;
    border-radius: 0%;
    border: none;
}
.cursor{
    cursor: pointer;
}
.btn-group{
    margin-top: 10px;
    margin-left: 5px;
    margin-right: 5px;
    
}
.display{
    display: inline-flex;
}
.btn-default{
    border-color: white;
    border-radius: 0%;
}